.imgSliderRightArrowStyles  {
  position: absolute;
  top: 50%;
  right: 2vw;
  transform: translateY(-50%);
  font-size: 3.5vw;
  color: #fff;
  z-index: 2;
  cursor: pointer;

}

.imgSliderLeftArrowStyles  {
  position: absolute;
  top: 50%;
  left: 2vw;
  transform: translateY(-50%);
  font-size: 3.5vw;
  color: #fff;
  z-index: 2;
  cursor: pointer;

}

@media (max-width: 768px) { 

  .imgSliderRightArrowStyles, .imgSliderLeftArrowStyles  {
      font-size: 10vw;
      
  }
}
