@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&family=Oswald:wght@700&display=swap');


.bannerContainer {
  display: flex;
  justify-content: space-around;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  flex-wrap: wrap; /* allows the items to wrap on smaller screens */
}

.imageWithText {
  flex: 1 1 30%; /* grow, shrink, base size */
  position: relative;
  text-align: center;
  margin: 0.5%;
}

.image_style {
  width: 100%;
  border-radius: 4px;
  border: 1px solid #bbb;
  transition: all 0.3s linear;
  cursor: pointer;
  aspect-ratio: 2/1;
  /* object-fit: cover; important to maintain aspect ratio */
}

.image_style:hover {
  box-shadow: rgba(255, 255, 255, 0.8) 0px 5px 15px, inset rgba(255, 255, 255, 0.15) 0px -10px 20px;
  transform: scale(1.01);
}

.imageText {
  position: absolute;
  left: 50%;
  bottom: 65%;
  transform: translateX(-50%);
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  font-family: 'Oswald', sans-serif;
  font-size: clamp(0.7vw, 1.7vw, 3.2vw);
  text-shadow: 2px 2px 3px rgb(0, 0, 0);
}

@media (max-width: 768px) {
  .bannerContainer {
      flex-direction: column;
      
  }

  .imageText {
    bottom: 55%;  /* Adjust as needed */
    font-size:clamp(1.7vw, 5vw, 6vw);  /* Adjust as needed */
  }
}

